body {
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: 'Exo 2';
  font-style: normal;
  font-weight: 100;
  src: url(../../static/media/exo-2-v5-cyrillic-100.4e556de1.eot); /* IE9 Compat Modes */
  src: local('Exo 2 Thin'), local('Exo2-Thin'),
       url(../../static/media/exo-2-v5-cyrillic-100.4e556de1.eot?#iefix) format('embedded-opentype'), 
       url(../../static/media/exo-2-v5-cyrillic-100.09554dbf.woff2) format('woff2'), 
       url(../../static/media/exo-2-v5-cyrillic-100.f770d64d.woff) format('woff'), 
       url(../../static/media/exo-2-v5-cyrillic-100.557a8837.ttf) format('truetype'), 
       url(../../static/media/exo-2-v5-cyrillic-100.db29d761.svg#Exo2) format('svg'); /* Legacy iOS */
}
.Page {
  padding: 20px 20px 10px;
  display: -webkit-flex;
  display: flex;
  position: relative;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  min-height: calc(100vh - 30px);
  font-family: "Trebuchet MS", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Tahoma, sans-serif;
  font-weight: 400;
  background-position: 0 0;
  z-index: 0;
}

.Page::before {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  background: -webkit-linear-gradient(
    top,
    rgba(0,0,0,0.5) 10%,
    rgba(0,0,0,0) 30%,
    rgba(0,0,0,0) 70%,
    rgba(0,0,0,0.5) 100%
    );
  background: linear-gradient(
    to bottom,
    rgba(0,0,0,0.5) 10%,
    rgba(0,0,0,0) 30%,
    rgba(0,0,0,0) 70%,
    rgba(0,0,0,0.5) 100%
    );
  z-index: -1;
}

.Page-Title {
  margin: 0;
  display: block;
  color: #ffffff;
  font-family: "Exo 2";
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  text-shadow: 0 1px 0 #000000;
}

.Page-Product {
  margin-top: 23px;
  max-width: 1200px;
}
.Product {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-left: 80px;
  margin-right: 80px;
}
.Product-Item {
  margin-left: 80px;
  margin-bottom: 23px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  box-sizing: border-box;
  outline: none;
}

.Product-Item:first-of-type {
  margin-left: 0;
}


.Product-Item_status_disabled {
  pointer-events: none;
}

.Product-Card {
  padding: 20px 45px 0;
  margin-bottom: 19px;
  width: 312px;
  height: 472px;
  position: relative;
  color: #666666;
  font-family: "Trebuchet MS";
  background-color: #f2f2f2;
  background:-webkit-linear-gradient(315deg, transparent 30px, #f2f2f2 0);
  background:linear-gradient(135deg, transparent 30px, #f2f2f2 0);
  border-radius: 10px;
  box-sizing: border-box;
  cursor: pointer;
}

.Product-Card::before {
  width: calc(100% + 8px);
  height: calc(100% + 8px);
  position: absolute;
  top: -4px;
  left: -4px;
  content: '';
  background: -webkit-linear-gradient(315deg, transparent 31px, #1698d9 0);
  background: linear-gradient(135deg, transparent 31px, #1698d9 0);
  border-radius: 10px;
  box-sizing: border-box;
  z-index: -1;
  -webkit-transition: background 0.25s ease;
  transition: background 0.25s ease;
}

.Product-Item:hover .Product-Card::before {
  background: -webkit-linear-gradient(315deg, transparent 31px, #2ea8e6 0);
  background: linear-gradient(135deg, transparent 31px, #2ea8e6 0);
}

.Product-Item_selected:hover .Product-Card::before {
  background: -webkit-linear-gradient(315deg, transparent 31px, #e62e7a 0);
  background: linear-gradient(135deg, transparent 31px, #e62e7a 0);
}

.Product-Item_status_disabled:hover .Product-Card::before {
  background: -webkit-linear-gradient(315deg, transparent 31px, #b3b3b3 0);
  background: linear-gradient(135deg, transparent 31px, #b3b3b3 0);
}

.Product-Item_status_disabled .Product-Card::before {
  background: -webkit-linear-gradient(315deg, transparent 31px, #b3b3b3 0);
  background: linear-gradient(135deg, transparent 31px, #b3b3b3 0);
}

.Product-Item_selected .Product-Card::before {
  background: -webkit-linear-gradient(315deg, transparent 31px, #d91667 0);
  background: linear-gradient(135deg, transparent 31px, #d91667 0);
}

.Product-Description {
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
}

.Product-Item_selected .Product-DescriptionText {
  display: none;
}

.Product-Item_selected:hover .Product-DescriptionText {
  display: block;
}

.Product-Hint {
  display: none;
  color: #e62e7a;
}

.Product-Item_selected:hover .Product-Hint {
  display: none;
}

.Product-Item_selected .Product-Hint {
  display: block;
}

.Product-Item_status_disabled .Product-Description {
  opacity: 0.5;
}

.Product-Company {
  margin-top: 4px;
  color: #000000;
  font-size: 48px;
  line-height: 48px;
  font-weight: 700;
}

.Product-Item_status_disabled .Product-Company {
  opacity: 0.5;
}

.Product-Taste {
  margin-top: 3px;
  color: #000000;
  font-size: 24px;
  line-height: 24px;
  font-weight: 700;
}

.Product-Item_status_disabled .Product-Taste {
  opacity: 0.5;
}

.Product-Promo {
  margin-top: 13px;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}


.Product-Item_status_disabled .Product-Promo {
  opacity: 0.5;
}

.Product-Portion {
  margin: 0;
}

.Product-Gift {
  margin: 0;
  max-width: 125px;
}


.Product-Image {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0px;
}

.Product-Item_status_disabled .Product-Image {
  opacity: 0.5;
}

.Product-Serving {
  position: absolute;
  bottom:11px;
  right: 11px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 81px;
  height: 81px;
  background-color: #1698d9;
  border-radius: 50%;
  color: #ffffff;
  font-size: 42px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
  -webkit-transition: background-color 0.25s ease;
  transition: background-color 0.25s ease;
}

.Product-Item_status_disabled .Product-Serving {
  background-color: #b3b3b3;
}

.Product-Item_selected .Product-Serving {
  background-color: #d91667;
}

.Product-Item:hover .Product-Serving {
  background-color: #2ea8e6;
}

.Product-Item_selected:hover .Product-Serving {
  background-color: #e62e7a;
}

.Product-Item_status_disabled:hover .Product-Serving {
  background-color: #b3b3b3;
}

.Product-Weight {
  margin-top: 5px;
}

.Product-Units {
  font-size: 21px;
  margin-top: 5px;
}

.Product-Info {
  color: #ffffff;
  font-size: 13px;
  font-weight: 400;
  line-height: 16px;
  text-align: center;
}

.Product-Link {
  margin-left: 5px;
  color: #1698d9;
  text-decoration: none;
  border-bottom: 1px dashed #1698d9;
}

.Product-Item:hover .Product-Link {
  color: #2ea8e6;
  border-color: #2ea8e6;
}

.Product-Item_status_disabled .Product-Info {
  color: #ffff66;
}

@media screen and (max-width: 830px) {
  .Product-Item {
    margin-left: 0;
  }
}

@media screen and (max-width: 340px) {
  .Product-Card {
    width: 295px;
  }
}
